                    new_itmes.push(obj)
                }
            }

            that.tableData[i].r = new_itmes[0].trim()
            that.tableData[i].g = new_itmes[1].trim()
            that.tableData[i].b = new_itmes[2].trim()

        }
        console.log(this.tableData);
    };
},


    BiaoqianBaocun() {
      var elementA = document.createElement('a');
//
elementA.download = "" + ".txt";
elementA.style.display = 'none';
let arr = ['r'+'                    '+'g'+'                    '+'b'
,this.tableData[0].r+'    '+this.tableData[0].g+'    '+this.tableData[0].b
,this.tableData[1].r+'    '+this.tableData[1].g+'    '+this.tableData[1].b
,this.tableData[2].r+'    '+this.tableData[2].g+'    '+this.tableData[2].b
,this.tableData[3].r+'    '+this.tableData[3].g+'    '+this.tableData[3].b
,this.tableData[4].r+'    '+this.tableData[4].g+'    '+this.tableData[4].b
,this.tableData[5].r+'    '+this.tableData[5].g+'    '+this.tableData[5].b
,this.tableData[6].r+'    '+this.tableData[6].g+'    '+this.tableData[6].b


];
let result = arr.join("\r\n");
//blobjson      
var blob = new Blob([result]);
//blobURLahref
elementA.href = URL.createObjectURL(blob);
document.body.appendChild(elementA);
elementA.click();
document.body.removeChild(elementA);
    },

    addItem() {
