        name: " "
      }
      this.index++
      this.tableData.push(newline)
      this.$message({
        message: "",
        type: "success",
      });
    },
    printImage(image) {
      function replacer(key, value) {
        if (!!value && value.byteLength !== undefined) {
          return String(value.slice(0, 6)) + '...'
        }
        return value
      }
      outputTextArea.textContent = JSON.stringify(image, replacer, 4)
    },
    showcolor() {
      if (this.colorList.length != 0) {
        for (let i = 0; i <= 14; i++) {
          this.tableData[i].r = this.colorList[i][0];
          this.tableData[i].g = this.colorList[i][1];
          this.tableData[i].b = this.colorList[i][2];
        }
      }
    },
    showBody() {

      // for (let i = 0; i <= 15; i++) {
      //   this.BodyData[0][i]= 1;

      // }
      this.BodyData[0][1] = "150";
      this.BodyData[0][2] = "198";
      this.BodyData[0][3] = "190";
      this.BodyData[0][4] = "50";
      this.BodyData[0][5] = "30";
      this.BodyData[0][6] = "48";
      this.BodyData[0][7] = "800";
      this.BodyData[0][8] = "43";
      this.BodyData[0][9] = "45";
      this.BodyData[0][10] = "160";
      this.BodyData[0][11] = "189";
      this.BodyData[0][12] = "79";
      this.BodyData[0][13] = "78";
      this.BodyData[0][14] = "69";
      this.BodyData[0][15] = "102";

      for (let i = 0; i <= 15; i++) {
        this.BodyData[1][i] = 1;

      }

    },
    showView() {


      this.ViewData[0].X = "1:1";
      this.ViewData[0].Y = "1:1";
      this.ViewData[0].Z = "1:1";

      this.ViewData[1].X = "1:1.13";
      this.ViewData[1].Y = "1:1.13";
      this.ViewData[1].Z = "1:1.13";


    },
    remove() {
      document.querySelector('#container1').innerHTML = "";
      document.querySelector('#container2').innerHTML = "";
      document.querySelector('#container3').innerHTML = "";
      document.querySelector('#container').innerHTML = "";
      document.querySelector('#input').value = "";
      for (let i = 0; i <= 14; i++) {
        this.tableData[i].r = "";
        this.tableData[i].g = "";
        this.tableData[i].b = "";
      }
      for (let i = 0; i <= 1; i++) {
        this.ViewData[i].X = "";
        this.ViewData[i].Y = "";
        this.ViewData[i].Z = "";
      }
      for (let i = 0; i <= 15; i++) {
        this.BodyData[0][i] = "";

      }
      for (let i = 0; i <= 15; i++) {
        this.BodyData[1][i] = "";

      }

    },
    render(source, judge) {
      const view3d = document.querySelector('#container');
      const fullScreenRenderer = vtkFullScreenRenderWindow.newInstance({
        rootContainer: view3d,
        containerStyle: {
          height: '100%',
          overflow: 'hidden',
        },
        background: [0, 0, 0],
      });
      const renderer = fullScreenRenderer.getRenderer();
      const renderWindow = fullScreenRenderer.getRenderWindow();
      // renderWindow.getInteractor().setDesiredUpdateRate(15);
      const actor = vtkVolume.newInstance();
      const mapper = vtkVolumeMapper.newInstance();
      mapper.setInputData(source);
      actor.setMapper(mapper);
      // mapper.setSampleDistance(0.7);
      const sampleDistance =
        0.7 *
        Math.sqrt(
          source
            .getSpacing()
            .map((v) => v * v)
            .reduce((a, b) => a + b, 0),
        );
      mapper.setSampleDistance(sampleDistance);
      const activeCamera = renderer.getActiveCamera();
      activeCamera.setPosition(0, -1, 0);
      activeCamera.setFocalPoint(0, 0, 0);
      activeCamera.setViewUp(0, 0, 1);

      const lookupTable = vtkColorTransferFunction.newInstance();
      const piecewiseFun = vtkPiecewiseFunction.newInstance();

      const newPreset = {
        "ColorSpace": "Diverging",
        "NanColor": [1, 1, 0],
        "RGBPoints": [
          0,
          0.23137254902,
          0.298039215686,
          0.752941176471,
          0.5,
          0.865,
          0.865,
          0.865,
          1,
          0.705882352941,
          0.0156862745098,
          0.149019607843
        ]
      };
      // lookupTable.applyColorMap(vtkColorMaps.getPresetByName('Cool to Warm'));//
      lookupTable.applyColorMap(newPreset);
      if (judge == 1) {
        lookupTable.setMappingRange(0, 256);
        lookupTable.updateRange();
        for (let i = 0; i <= 8; i++) {
        piecewiseFun.addPoint(i * 32, i / 8);
      }
      }
      else {
        lookupTable.setMappingRange(0, 15);
        lookupTable.updateRange();

        for (let j = 0; j <= 15; j++) {
          const rgb = [];
          lookupTable.getColor(j, rgb)
          this.colorList.push(rgb)

        }
        console.log(this.colorList);
        this.showcolor();
        this.showBody();
        this.showView();
        piecewiseFun.addPoint(0, 0);
        for (let i = 1; i <= 15; i++) {
        piecewiseFun.addPoint(i , 0.7);}
      //   for (let i = 0; i <= 8; i++) {
      //   piecewiseFun.addPoint(i * 32, i / 8);
      // }
      }
      


      actor.getProperty().setRGBTransferFunction(0, lookupTable);
      actor.getProperty().setScalarOpacity(0, piecewiseFun);


      renderer.addActor(actor);
      renderer.resetCamera();
      renderWindow.render();


      const { SlicingMode } = ImageConstants;
      const container1 = document.querySelector('#container1');
      const genericRenderWindow1 = vtkGenericRenderWindow.newInstance();
      genericRenderWindow1.setContainer(container1);
      genericRenderWindow1.resize();
      const renderer1 = genericRenderWindow1.getRenderer();
      const renderWindow1 = genericRenderWindow1.getRenderWindow();
      const camera1 = renderer1.getActiveCamera();
      const istyle = vtkInteractorStyleImage.newInstance();
      istyle.setInteractionMode('IMAGE_SLICING');
      renderWindow1.getInteractor().setInteractorStyle(istyle);
      const actor1 = vtkImageSlice.newInstance();
      const mapper1 = vtkImageMapper.newInstance();
      mapper1.setSliceAtFocalPoint(true);
      mapper1.setInputData(source);
      mapper1.setSlicingMode(SlicingMode.X);
      actor1.setMapper(mapper1);
      actor1.getProperty().setColorWindow(255);
      actor1.getProperty().setColorLevel(127);
      renderer1.addActor(actor1);
      const position1 = camera1.getFocalPoint();
      // offset along the slicing axis
      const normal1 = mapper1.getSlicingModeNormal();
      position1[0] += normal1[0];
      position1[1] += normal1[1];
      position1[2] += normal1[2];
      camera1.setPosition(...position1);
      camera1.setViewUp([0, 0, 1]);
      camera1.setParallelProjection(true);
      renderer1.resetCamera();
      renderWindow1.render();

      const container2 = document.querySelector('#container2');
      const genericRenderWindow2 = vtkGenericRenderWindow.newInstance();
      genericRenderWindow2.setContainer(container2);
      genericRenderWindow2.resize();
      const renderer2 = genericRenderWindow2.getRenderer();
      const renderWindow2 = genericRenderWindow2.getRenderWindow();
      const camera2 = renderer2.getActiveCamera();

      const istyle2 = vtkInteractorStyleImage.newInstance();
      istyle2.setInteractionMode('IMAGE_SLICING');
      renderWindow2.getInteractor().setInteractorStyle(istyle2);

      const actor2 = vtkImageSlice.newInstance();
      const mapper2 = vtkImageMapper.newInstance();
      mapper2.setSliceAtFocalPoint(true);
      mapper2.setSlicingMode(SlicingMode.Z);//K
      mapper2.setInputData(source);
      // // tell the actor which mapper to use
      actor2.setMapper(mapper2);
      actor2.getProperty().setColorWindow(255);
      actor2.getProperty().setColorLevel(127);
      renderer2.addActor(actor2);
      const position2 = camera2.getFocalPoint();
      const normal2 = mapper2.getSlicingModeNormal();
      position2[0] += normal2[0];
      position2[1] += normal2[1];
      position2[2] += normal2[2];
      camera2.setPosition(...position2);
      camera2.setViewUp([0, 1, 0]);
      camera2.setParallelProjection(true);
      renderer2.resetCamera();
      renderWindow2.render();


      const container3 = document.querySelector('#container3');
      const genericRenderWindow3 = vtkGenericRenderWindow.newInstance();
      genericRenderWindow3.setContainer(container3);
      genericRenderWindow3.resize();
      const renderer3 = genericRenderWindow3.getRenderer();
      const renderWindow3 = genericRenderWindow3.getRenderWindow();
      const camera3 = renderer3.getActiveCamera();

      const istyle3 = vtkInteractorStyleImage.newInstance();
      istyle3.setInteractionMode('IMAGE_SLICING');
      renderWindow3.getInteractor().setInteractorStyle(istyle3);//

      const actor3 = vtkImageSlice.newInstance();
      const mapper3 = vtkImageMapper.newInstance();
      mapper3.setSliceAtFocalPoint(true);//
      mapper3.setSlicingMode(SlicingMode.Y);
      mapper3.setInputData(source);
      actor3.setMapper(mapper3);
      actor3.getProperty().setColorWindow(255);
      actor3.getProperty().setColorLevel(127);
      renderer3.addActor(actor3);
      const position3 = camera3.getFocalPoint();
      const normal3 = mapper3.getSlicingModeNormal();
      position3[0] += normal3[0];
      position3[1] += normal3[1];
      position3[2] += normal3[2];
      camera3.setPosition(...position3);
      camera3.setViewUp([0, 1, 0]);
      camera3.setParallelProjection(true);
      renderer3.resetCamera();
      renderWindow3.render();


    },
    dDicom(files) {
      return readImageDICOMFileSeries(null, files).then(({
        webWorker,
        image
      }) => {
        // printImage("source")
        const source = vtkITKHelper.convertItkToVtkImage(image);
        // printImage(imageData)
        this.render(source, 1)

      })

    },
    dNii(files, judge) {

      return readImageFile(null, files[0]).then(({ image, webWorker }) => {//null
        const source = vtkITKHelper.convertItkToVtkImage(image);
        this.render(source, judge)

      });
    },
    outputFileInformation(event) {
      const dataTransfer = event.dataTransfer
      const files = event.target.files || dataTransfer.files

      const type = files[0].name.substring(files[0].name.lastIndexOf(".") + 1)
      if (type == "dcm") {
        this.dDicom(files)
      }
      else if (type == "nii") {
        console.log(files[0].name)
        if (files[0].name == "after.nii") {
          this.dNii(files, 2)
        }
        else {
          this.dNii(files, 1)
        }
        console.log("1")
      }
      else if (type == "nii.gz") {
        console.log("2")
      }
      else if (type == "mhd") {
        console.log("3")
      }
      else if (type == "raw") {
        console.log("4")
      }
    },
    checkUpload() {
      this.isUpload = this.afterImg.length !== 0;
    },
    clearQueue() {
      this.fileList = [];
      this.$message.success("");
    },
    notvisible() {
      this.cutVisible = false;
      this.fileList = [];
    },
    getMore() {
      this.getUploadImg("");
    },
    uploadMore() {
      this.beforeUpload(...this.$refs.uploadFile.files)
      if (this.canUpload) {
        this.fileList.push(...this.$refs.uploadFile.files);
      } else {
        setTimeout(() => {
          this.$message.error('')
        }, 1000);

      }
    },
    fileClick() {
      document.querySelector("#folder").click();
    },
    beforeUpload(file) {
      this.cutVisible = this.$refs.cut.checked;
      const fileSuffix = file.name.substring(file.name.lastIndexOf(".") + 1)
      const whiteList = ['jpg', 'jpeg', 'png', 'JPG', 'JPEG']
      if (whiteList.indexOf(fileSuffix) === -1) {
        this.$message.error("jpg, jpeg, png, JPG, JPEG,");
        this.fileList = []
        this.canUpload = false
        this.cutVisible = false;
      }
      else {
        this.canUpload = true
        this.fileimg = window.URL.createObjectURL(new Blob([file]));
      }
    },
    select() {
      this.isNotCut = this.$refs.cut.checked;
    },
    setCurrent(row) {
      this.$refs.singleTable.setCurrentRow(row);
    },
    handleCurrentChange(val) {
      this.currentRow = val;
    },
  },

};
</script>
<style lang="less" scoped>
