  font-family: SimHei sans-serif;
}

#sub-title {
  font-size: 30px;
}

#sub-title:hover:after {
  left: 0%;
  right: 0%;
  width: 220px;

}

.clear-queue {
  position: absolute;
  left: 5px;
  top: 10%;
  z-index: 100;
}

.el-radio /deep/ {
  height: 62px;
}

.hang {
  margin-top: 0px;

}
</style>
